<script>
	// #ifdef H5
	import { shareConfig } from "./common/wechat";
	// #endif
	export default {
		onLaunch: function(options) {
			this.autoLogin();
			// #ifdef MP-WEIXIN
			this.parseSceneParams(options);
			this.$http.getTemplateConfig().then(res=>{
			  if(JSON.stringify(res.data) != "{}"){
				this.$storage.setJson('template', res.data);
			  }else{
				  this.$storage.remove('template');
			  }
			});
			// #endif
			
			// #ifdef H5
			shareConfig("home");
			let arr = window.location.href.split("#");
			if(arr[1] != undefined){
				let uu = arr[1].split("=")[0];
				if(uu.length >= 2 && uu[0] == "u"){
					this.$storage.set('spread_id',uu[1]);
				}
			}
			
			if(options.query.u != undefined && options.query.u > 0){
				this.$storage.set('spread_id',options.query.u);
			}
			// #endif
		},
		onShow: function() {
			let users = this.$storage.getJson("users");
			if(users){
				uni.setTabBarBadge({ index: 3, text: users.shop_count.toString() });
			}else{
				uni.removeTabBarBadge({ index: 3 })
			}
		},
		onHide: function() {
			console.log('App Hide')
		},
		methods: {
			autoLogin(){
				let users = this.$storage.getJson("users");
				if(users != null){
					this.$http.autoLogin().then(result=>{
						if(result.status===1000){
							this.$store.commit("UPDATEUSERS",result.data);
						}else{
							this.$storage.remove("users");
						}
					});
				}
			},
			// #ifdef MP-WEIXIN
			parseSceneParams(option){
				if (option.query.hasOwnProperty('scene')){
				  switch(option.scene){
					case 1001: // 直接进入小程序
					case 1011: // 扫码
					case 1012: // 长按图片识别二维码
					case 1013: // 手机相册选取二维码
					case 1047: // 扫描小程序码
					case 1048: // 长按图片识别小程序码
					case 1049: // 手机相册选取小程序码
					  let scene = decodeURIComponent(option.query.scene);
					  let res = {};
					  let arr = scene.split("&");
					  for (let i = 0; i < arr.length; i++) {
						let param = arr[i].split("=");
						res[param[0].replace(/(^\s*)|(\s*$)/g, "")] = decodeURIComponent(param[1].replace(/(^\s*)|(\s*$)/g, ""));
					  }
					  
					  if((res.u == null || res.u == undefined) || res.u <= 0){
						return ;
					  }
					  
					  this.$storage.set('spread_id', res.u);
				  }
				}
			},
			// #endif
		}
	}
</script>

<style>
	@import '~@/style.css';
	page { background-color: #f8f8f8; }
	.clear:after { content:" "; font-size:0; display:block; height:0; clear:both; visibility:hidden; }
	uni-checkbox .uni-checkbox-input { border-radius: 50rpx; }
	// #ifdef H5
	uni-toast { z-index: 10099 !important; }
	uni-modal { z-index: 10099 !important; }
	// #endif
	::-webkit-scrollbar {
	  display: none;
	  width: 0 !important;  
	  height: 0 !important;  
	  -webkit-appearance: none;  
	  background: transparent;  
	}
</style>
